h1 {
    color: $blue;
    font-size:36px;
}

h2 {
    color: $purple;
    font-size:32px;
}

h3 {
    color: $black;
    text-transform:uppercase;
    font-size:26px;
    letter-spacing:2px;
}

h4 {
    color: $blue;
    font-size:22px;
}

h5 {
    color: $black;
    font-size:20px;
}

h6 {
    color: $black;
    font-style: italic;
    font-size:18px;
}

h1, h2, h3, h4, h5, h6{
    font-family:"Open Sans";
    font-weight:bold;
}

p {
    font-family:"Merriweather";
    color:$black;
    font-size:16px;
    line-height:26px;

    &.intro-text {
        font-size:18px;
        line-height:28px;
    }
}

small {
    color:$black;
    font-size:11px;
    font-size:11px;
}