
    .gform_wrapper {
        margin:0px;
    }
    .gfield_error {
        .ginput_container {
            input, select {
                border: 1px solid #ff0000;
            }
        }
    }

    .gfield {

        position:relative;
        .gfield_label {
            position:absolute;
            top:0px;
            left:0px;
            text-transform:uppercase;
            font-size:10px;
            font-weight:600;
            margin:5px;
            font-family:"Open Sans";
        }

        .ginput_container {
            margin-bottom:10px;
            input {
                width:100%;
                height:100%;
                padding:20px 5px 5px;
                @mixin placeholder {
                    letter-spacing:3px;
                }
            }
            .gfield_checkbox, .gfield_radio {
                @mixin clear;
                label {

                }
                li {
                    float:left;
                    margin-right:10px;
                }
                input {
                   display:none;
                }


    }
        }

        &.purple {
            background:$purple;
            .ginput_container_select {
                &:after{
                    border-top-color:$white !important;
                }
                select{
                    color:$white;
                    option {
                        background:$purple;
                    }
                }
            }
        }

        .ginput_container_select {
            select {
                position:relative;
                appearance:none;
                width:100%;
                height:100%;
                padding-right:50px;
                background:none;
                z-index:1;

            }
            &:after {
                position:absolute;
                top:50%;
                right:10px;
                display:block;
                content:" ";
                width: 0;
                height: 0;
                border-left: 8px solid transparent;
                border-right: 8px solid transparent;
                border-top:8px solid $black;
                margin-top:-4px;
            }

            &:before {
                position:absolute;
                top:0;
                right:0;
                display:block;
                content:" ";

                border:1px solid $gray;
                width:40px;
                height:100%;
            }
        }
    }

    input[type=checkbox] + label, input[type=radio] + label {
        font-family:'Open Sans';
        color:$black;
    }


    .disabled + label:before {
        border:solid 1px #dcdcdc !important;

    }
    .disabled:checked + label {
        color:#dcdcdc;
    }
    .disabled:checked + label:before {
        background:#dcdcdc !important;

    }
    input[type=checkbox] { display:none; } /* to hide the checkbox itself */
    input[type=checkbox] + label:before {
        display: inline-block;
        font-family: 'icomoon';
        color:$white;
        font-size:12px;


    }

    input[type=checkbox] + label:before {
        margin-right:5px;
        content:'\e914';
        text-align:center;
        padding-top:1px;
        width:15px;
        height:15px;
        border:solid 1px $gray;
    } /* unchecked icon */

    input[type=radio] + label:before {
        margin-right:5px;
        content:' ';
        text-align:center;
        width:5px;
        height:5px;
        display:inline-block;
        border-radius:50%;
        outline: 2px solid #CCC;
        outline-offset:10px;
        box-shadow: 7px 7px 0 2px $gray;
    } /* unchecked icon */
    input[type=checkbox]:checked + label:before {
        content:'\e914';
        background:$blue_light;
        border-color:$blue_light;
    } /* checked icon */




.ginput_container {
    input, select {
        font-size:16px;
        line-height:28px;
        padding:.313em 0;
        border:1px solid $gray;
    }
    select {
        padding:9px;
    }
}
.gform_wrapper {
    @mixin desktop {
        margin: 20px;
    }
}

.gform_body {
    @mixin clear;
}

.gform_footer {
    margin:20px 0;
}

.gfield_label {
    font-weight:bold;
    margin:.625em 0 .5em;
    @mixin clear;

    display:block;
}
.gfield_required {
    color:#790000;
    margin-left:2px;
    display:inline-block;
}